import {TemplateRef} from '@angular/core';
import {KeyValueObject} from '../common/common.models';

export interface ButtonConfig {
  /**
   * Button title
   */
  name?: string;
  value?: string | number;
  /**
   * Only valid for the button group of type 'tile' or 'color-picker'.
   * Used to specify button content
   * @since version 10.18.0
   */
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  templateRef?: TemplateRef<any>;
  /**
   * A context object which can be passed to customize the swatch element template
   * @since v10.18.0
   */
  context?: KeyValueObject;
  /**
   * Button background color.
   * Only valid for the button group of type 'tile' and 'color-picker'
   * @since v10.18.0
   */
  background?: string;
  /**
   * Custom css class to be added to the button.
   * Only valid for the button group of type 'tile' and 'color-picker'
   * @since v10.18.0
   */
  customClass?: string;
  /**
   * Button background color.
   * Only valid for the button group of type 'tile' and 'color-picker'
   * @since v10.18.0
   */
  disabled?: boolean;
  /**
   * "aria-label" attribute for button element
   * @since v12.11.0
   */
  ariaLabel?: string;
}

export interface InnerConfigType {
  index?: number;
  name?: string;
  value?: string | number;
  disabled?: boolean;
  isActive?: boolean;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  templateRef?: TemplateRef<any>;
  context?: KeyValueObject;
  background?: string;
  customClass?: string;
  darkcolor?: boolean;
  ariaLabel?: string;
}

export interface CheckmarkColorType {
  dark: string;
  light: string;
}

export type ButtonSize = 'xtra-large' | 'xtra-small' | 'small' | 'large' | 'block';

export type ButtonGroupType = 'button' | 'tile' | 'color-picker';
